html {
  font-size: 62.5%;
  box-sizing: border-box;
}
@media (max-width: 280px) {
  html {
    font-size: 8px;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

/** Globales **/
img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.contenedor {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Botones **/
.boton {
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  font-weight: 700;
  padding: 1rem 3rem;
  display: inline-block;
  text-align: center;
  border: none;
  margin-top: 3rem;
}

.boton:hover {
  cursor: pointer;
}

.boton1 {
  background: #f08902e7;
}

.boton2 {
  background: #71b100;
}

.boton-enviar {
  display: flex;
  justify-content: flex-end;
}

/** Utilidades **/
.seccion {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.fw-300 {
  font-weight: 300;
}

.centrar-texto {
  text-align: center;
}

.d-block {
  display: block !important;
}

.texto-centrado {
  text-align: justify;
  font-size: 1.8rem;
}

.contenido-centrado {
  max-width: 100rem;
  margin: 0 auto;
}

/** Pagina Nosotros **/
@media (min-width: 768px) {
  .contenido-nosotros img {
    max-width: 45rem;
    float: left;
    margin-right: 2rem;
  }
}
@media (min-width: 1200px) {
  .contenido-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
  .contenido-nosotros img {
    max-width: 100%;
    float: none;
    margin-right: 0;
  }
}

.texto-nosotros blockquote {
  font-weight: 700;
  margin: 0;
  font-size: 2rem;
}

/** Pagina Anuncio **/
.resumen-propiedad {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/** Pagina Contactos **/
legend {
  font-size: 2.8rem;
  color: #222222;
}

label {
  display: block;
  text-transform: uppercase;
  font-weight: 700;
}

form p {
  margin: 2rem 0 0 0;
}

input:not([type=submit]),
textarea,
select {
  display: block;
  width: 100%;
  background: rgba(204, 204, 204, 0.7);
  padding: 1rem;
  margin-bottom: 2rem;
  border: none;
}

input[type=radio] {
  width: auto;
  margin: 0;
}

textarea {
  height: 20rem;
}

select {
  appearance: none;
}

.forma-contacto {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30rem;
}

/** Anuncios **/
@media (min-width: 878px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}
@media (min-width: 1300px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
  }
}

.anuncio {
  margin: 0 auto;
  border: 1px solid rgba(181, 181, 181, 0.9);
  background: #f5f5f5;
  margin-bottom: 2rem;
}

.contenido-anuncio {
  padding: 2rem;
}

.contenido-anuncio h3,
.contenido-anuncio p {
  margin: 0;
}

.contenido-anuncio h3 {
  font-size: 2.8rem;
  font-weight: 300;
}

.precio {
  font-weight: 700;
  color: #71b100;
}

.iconos-caracteristicas {
  display: flex;
  list-style-type: none;
  justify-content: space-evenly;
  text-align: center;
  align-items: center;
  padding: 0;
  max-width: 50rem;
  flex: 1;
}

.iconos-caracteristicas li {
  display: flex;
}

.iconos-caracteristicas img {
  margin-right: 2rem;
}

.ver-todas {
  display: flex;
  justify-content: flex-end;
}

/** Seccion Inferior **/
@media (min-width: 1200px) {
  .seccion-inferior {
    display: flex;
    justify-content: space-between;
  }
  .seccion-inferior .blog {
    flex-basis: 60%;
  }
  .seccion-inferior .testimoniales {
    flex-basis: calc(40% - 4rem);
  }
}

@media (min-width: 768px) {
  .entrada-blog {
    display: flex;
    justify-content: space-between;
    margin-bottom: 3rem;
  }
  .entrada-blog:last-child {
    margin-bottom: 0;
  }
  .entrada-blog .imagen {
    flex-basis: 40%;
  }
  .entrada-blog .texto-entrada {
    flex-basis: calc(60% - 3rem);
  }
}

.texto-entrada a {
  color: #000000;
}

.texto-entrada h4 {
  margin: 0;
  line-height: 3rem;
}

.texto-entrada h4 {
  margin: 0;
  line-height: 3rem;
}

.texto-entrada h4::after {
  content: "";
  background: #71b100;
  display: block;
  margin-top: 1rem;
  height: 0.5rem;
}

.texto-entrada span {
  color: rgba(240, 137, 2, 0.904);
  font-weight: 700;
  font-style: italic;
}

.testimonial {
  background-color: #619800;
  padding: 2rem;
  color: #ffffff;
  font-size: 1.8rem;
  border-radius: 3rem;
}

.testimonial p {
  text-align: right;
  margin-inline-end: 1rem;
}

.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-image: no-repeat;
  background-size: cover;
  width: 6rem;
  height: 6rem;
  position: absolute;
  left: -4rem;
  top: -1rem;
}

.testimonial blockquote {
  /* Repasar posicionamiento */
  position: relative;
  padding-left: 3rem;
}

/** Footer **/
.footer-principal {
  background-color: #222222;
  margin-top: 5rem;
  padding: 3rem 0;
}

.contenido-footer {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

/** Header **/
.no-webp .inicio {
  background: #222222 url(../img/header.jpg);
}

.webp .inicio {
  background: #222222 url(../img/header.webp);
}

.site-header {
  background-color: #222222;
  padding: 3rem 0;
}
.site-header.inicio {
  height: 60rem;
  box-sizing: content-box;
  background-position: center center;
  background-size: cover;
}
@media (min-width: 1200px) {
  .site-header.inicio {
    box-sizing: border-box;
    height: 90vh;
  }
}

.contenido-header {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: center;
  margin-top: 4rem;
  color: #ffffff;
  max-width: 70rem;
  margin: 0;
  font-weight: normal;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-weight: bold;
  }
}

/** Navegación **/
.barra {
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.barra .display-mobile-menu {
  flex: 1;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .barra .display-mobile-menu {
    flex: 0 0 auto;
  }
}
.barra .display-mobile-menu > a {
  flex: 1;
  text-align: center;
  padding-left: 4.5rem;
}
.barra .display-mobile-menu > a img {
  margin: 0 auto;
  display: grid;
  place-items: center;
}
@media (min-width: 768px) {
  .barra .display-mobile-menu > a {
    padding-left: 0;
  }
}

.navegacion {
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    background-color: transparent;
  }
}
.navegacion a {
  text-decoration: none;
  transition: 0.4s;
  color: rgba(255, 255, 255, 0.6);
}
@media (min-width: 768px) {
  .navegacion a {
    font-size: 1.8rem;
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 768px) {
  .navegacion a:hover, .navegacion a:focus {
    color: white;
  }
}

.mobile-menu {
  position: absolute;
  width: 3.5rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  display: grid;
  place-items: center;
}
.mobile-menu img:hover {
  cursor: pointer;
}

.sticky-menu .navegacion {
  position: fixed;
  z-index: 1;
  display: block;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: rgba(123, 185, 15, 0.9);
  font-weight: bold;
  width: 95%;
  box-shadow: 0.2rem 0.3rem 0.4rem rgba(181, 181, 181, 0.9);
  padding-top: 20%;
  animation: mymove 1s linear;
  overflow-x: hidden;
}
.sticky-menu .navegacion a {
  font-size: 2rem;
  display: block;
  animation: mymove2 1s linear;
  padding-top: 1rem;
}
.sticky-menu .navegacion a::after {
  content: "";
  display: block;
  width: 80%;
  margin: 1rem auto;
  height: 0.1rem;
  background-color: #f5f5f5;
  margin-bottom: 0;
}
.sticky-menu .navegacion a:hover, .sticky-menu .navegacion a:focus {
  background-color: rgba(255, 255, 255, 0.2);
  color: #ffffff;
}
.sticky-menu .mobile-menu {
  z-index: 2;
  position: fixed;
}

.container-right {
  display: flex;
  align-items: center;
}
.container-right .dark-mode-boton {
  display: grid;
  place-items: center;
  width: 3rem;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .container-right .dark-mode-boton {
    margin-left: 2rem;
  }
}
.container-right .dark-mode-boton:hover {
  cursor: pointer;
}

@keyframes mymove {
  from {
    width: 0px;
  }
  to {
    width: 95%;
  }
}
@keyframes mymove2 {
  from {
    transform: translate(-10rem);
  }
  to {
    transform: translate(0);
  }
}
/** Iconos-Nosotros **/
.iconos-nosotros {
  text-align: center;
}
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
  }
}

.icono {
  margin-bottom: 2rem;
}
.icono h3 {
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .icono:last-of-type {
    grid-column: 1/3;
  }
}
@media (min-width: 1024px) {
  .icono {
    margin-bottom: 0;
  }
  .icono:last-of-type {
    grid-column: auto;
  }
}

/** Contacto Home **/
.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.imagen-contacto {
  box-sizing: content-box;
  background: url(../img/encuentra.jpg);
  background-position: center center;
  background-size: cover;
  height: 40rem;
  display: flex;
  align-items: center;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .imagen-contacto {
    box-sizing: border-box;
    padding: 0;
  }
}

.contenido-contacto {
  color: #ffffff;
  text-align: center;
  max-width: 90%;
}
@media (min-width: 768px) {
  .contenido-contacto {
    text-align: left;
    flex: 1;
  }
}
.contenido-contacto h2 {
  margin: 0;
}

.contenido-contacto p {
  font-size: 1.8rem;
}

.dark-mode {
  background-color: #090909;
}
.dark-mode p {
  color: #ffffff;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #f5f5f5;
}
.dark-mode .iconos-nosotros {
  background-color: transparent;
}
.dark-mode .anuncio {
  background-color: #222222;
  border: 1px solid #222222;
}
.dark-mode .anuncio .precio {
  color: #71b100;
}
.dark-mode .iconos-caracteristicas img {
  filter: invert(100%);
}
.dark-mode .testimonial {
  background-color: #507e00;
}